/* Canvas container */
.canvas {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  user-select: none;
}

/* Debug coordinates */
.debug-coordinates {
  position: absolute;
  top: 10px;
  left: 10px;
  color: black;
  font-size: 16px;
  font-family: Arial, sans-serif;
  z-index: 10; /* Ensure it's always on top */
}

/* Grid background */
.grid {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, #f7f7d4 1px, transparent 1px),
              linear-gradient(#f7f7d4 1px, transparent 1px);
  background-size: 50px 50px;
  z-index: 1;
  transition: background-position 0.1s linear; /* Smooth transition for grid movement */
}

/* Timeline container */
.timeline {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 2px;
  z-index: 2; /* Ensure the timeline is above the grid */
}

/* Future line */
.line.future-line {
  position: absolute;
  top: -1px;
  height: 2px;
  width: 100%;
  background-color: gray;
}

/* Past line */
.line.past-line {
  position: absolute;
  top: 1px;
  height: 2px;
  width: 100%;
  background-color: cyan;
}
